ный и операции', html: '<button onclick="test()">Жми!!!</button>\n<p id="line">Some text</p>', css: '', js: "function test(){\n  var line = document.getElementById('line')\n  if(line.style.display == \"none\") {\n    line.style.display = \"block\"\n  } else {\n    line.style.display = \"none\"\n  }\n}" }, { title: 'Переменный и операции', html: '<button onclick="test()">Жми!!!</button>\n<p id="line">Some text</p>', css: '#line{border: 1rem solid green;}\n#line:hover{border-color: blue;}', js: "function test(){\n  var line = document.getElementById('line')\n  line.style.borderColor = \"red\"\n}" }, { title: 'Переменный и операции', html: '<button onclick="test()">Жми!!!</button>\n<p id="line">Some text</p>', css: '#line{border: 1rem solid green;}\n#line:hover{border-color: blue!important;}', js: "function test(){\n  var line = document.getElementById('line')\n  line.style.borderColor = \"red\"\n}" }, { title: 'Переменный и операции', html: '<button onclick="test()">Жми!!!</button>\n<ul id="line"></ul>', css: '\n', js: "function test(){\n  var line = document.getElementById('line')\n  line.innerHTML += \"<li>1</li>\"\n  line.innerHTML += \"<li>2</li>\"\n  line.innerHTML += \"<li>3</li>\"\n  line.innerHTML += \"<li>4</li>\"\n}" }, { title: 'Переменный и операции', html: '<button onclick="test()">Жми!!!</button>\n<ul id="line"></ul>', css: '\n', js: "function test(){\n  var line = document.getElementById('line')\n  for(var i=1;i<10;i++){\n    line.innerHTML += \"<li>\"+i+\"</li>\"\n  }\n}" }, { title: 'Переменный и операции', html: '<button onclick="test()">Жми!!!</button>\n<ul id="line"></ul>', css: '\n', js: "function test(){\n  var line = document.getElementById('line')\n  var i = 1\n  while(i<10){\n    line.innerHTML += \"<li>\"+i+\"</li>\"\n    i++\n  }\n}" }, { title: 'Переменный и операции', html: '<button onclick="test()">Жми!!!</button>\n<ul id="line"></ul>', css: '\n', js: "function test(){\n  var line = document.getElementById('line')\n  var arr = [\"двойка\", \"тройка\", \"четверка\"]\n  line.innerHTML += \"<li>\"+arr[0]+\"</li>\"\n  line.innerHTML += \"<li>\"+arr[1]+\"</li>\"\n  line.innerHTML += \"<li>\"+arr[2]+\"</li>\"\n}" }, { title: 'Переменный и операции', html: '<button onclick="test()">Жми!!!</button>\n<ul id="line"></ul>', css: '\n', js: "function test(){\n  var line = document.getElementById('line')\n  var arr = [\"двойка\", \"тройка\", \"четверка\"]\n  for(var i=0;i<arr.length;i++){\n    line.innerHTML += \"<li>\"+arr[i]+\"</li>\"\n  }\n}" }, { title: 'Переменный и операции', html: '<button onclick="test()">Жми!!!</button>\n<ul id="line"></ul>', css: '\n', js: "function test(){\n  var line = document.getElementById('line')\n  var arr = [\"двойка\", \"тройка\", \"четверка\"]\n  for(var i in arr){\n    line.innerHTML += \"<li>\"+arr[i]+\"</li>\"\n  }\n}" }, { title: 'Переменный и операции', html: '<button onclick="test()">Жми!!!</button>\n<p>Lerem</p><p>ipsum</p><p>dolor</p>', css: '\n', js: "function test(){\n  var many_p= document.getElementsByTagName('p')\n  for(var i in many_p){\n    many_p[i].innerHTML +=1\n    many_p[i].style.color=\"#005500\"\n  }\n}" }, { title: 'Переменный и операции', html: '<button onclick="test()">Жми!!!</button>\n<p>Lerem</p><p>ipsum</p><p>dolor</p>', css: '.green{color: #005500;}', js: "function test(){\n  var many_p= document.getElementsByTagName('p')\n  for(var i in many_p){\n    many_p[i].classList.add(\"green\")\n  }\n}" }, { title: '', html: '<button onclick="test()">Жми!!!</button>\n<p>Lerem</p><p>ipsum</p><p>dolor</p>', css: '.green{color: #005500;}', js: "function test(){\n  var many_p= document.getElementsByTagName('p')\n  for(var i in many_p){\n    many_p[i].classList.toggle(\"green\")\n  }\n}" }, { title: '', html: '<button onclick="test(this)">Жми!!!</button>', css: '\n', js: 'function test(el){\n  el.style.color = "green"\n}' }, { title: '', html: '<button id="btn">Жми!!!</button>', css: '\n', js: 'function test(){\n  this.style.color = "green"\n}\nvar btn = document.getElementById("btn")\nbtn.onclick = test' }, { title: '', html: '<button>Жми!!!</button><button>Жми!!!</button>\n<button>Жми!!!</button><button>Жми!!!</button>\n', css: '\n', js: 'function test(){\n  this.style.color = "green"\n}\nvar btns = document.getElementsByTagName("button")\nfor(var i in btns){\n  btns[i].onclick = test\n}' }, { title: '', html: '<button class="gr">Жми!!!</button>\n<button>Жми!!!</button>\n<button class="gr">Жми!!!</button>\n<button>Жми!!!</button>\n', css: '\n', js: 'function test(){\n  this.style.color = "green"\n}\nvar btns = document.getElementsByClassName("gr")\nfor(var i in btns){\n  btns[i].onclick = test\n}' }, { title: '', html: '<button class="gr">Жми!!!</button>\n<button>Жми!!!</button>\n<button class="gr">Жми!!!</button>\n<button>Жми!!!</button>\n', css: '\n', js: 'function test(){\n  this.style.color = "green"\n}\nvar btns = document.querySelectorAll(".gr")\nfor(var i in btns){\n  btns[i].onclick = test\n}' }]
