  },
  computed: {
    slide () {
      return this.slides[this.slide_number - 1]
    },
    slide_code () {
      let str = '<head><style>*{font-size:' + this.font_size + 'px;}' + this.slide.css + '</style></head>'
      str += '<body>' + this.slide.html
      str += '<script>' + this.slide.js + '<$$$script>'.replace('$$$', '/') + '</body>'
      return str
    },
    slides_len () {
      return this.slides.length
    },
    slides_txt () {
      return JSON.stringify(this.slides)
    },
    code_download () {
      return 'text/json;charset=utf-8,' + encodeURIComponent(JSON.stringify(this.slides))
    }
  },
