  methods: {
    highlighter (lang) {
      console.log(lang)
      // console.log(languages)
      // console.log(languages['css'])
      const f = function (code) { return highlight(code, languages[lang]) } // languages.<insert language> to return html with markup
      console.log(f)
      return f
    },
