      this.copy = false
      this.slide_number *= 1
      this.slide_number += this.slide_number < this.slides.length ? 1 : 0
      console.log(this.slide_number)
    },
    prev () {
      // copy = false;
      this.slide_number *= 1
      this.slide_number -= this.slide_number > 1 ? 1 : 0
    },
