    // })
    },
    home () {
      if (confirm('Вы уверены?')) {
        this.slides = false
        this.slide_number = 1
      }
    },
    slide_move () {
      if (this.slide_move_to > 0 && this.slide_move_to !== this.slide_number) {
        this.array_move(this.slides, this.slide_number - 1, this.slide_move_to - 1)
        this.slide_move_to = 0
      }
    },
    array_move (arr, oldIndex, newIndex) {
      if (newIndex >= arr.length) {
        let k = newIndex - arr.length + 1
        while (k--) {
          arr.push(undefined)
        }
      }
      arr.splice(newIndex, 0, arr.splice(oldIndex, 1)[0])
      return arr // for testing
    }
  }
}
